<template>
    <el-menu :default-active="$route.path" class="el-menu-vertical-demo" background-color="#304156" text-color="#BFCBD9" active-text-color="#F56C6C" :collapse="isCollapse">           
        <h3>{{isCollapse?'后台':'通用后台管理系统'}}</h3>
        <template v-for="item in menuList">
            <template v-if="!item.children">
                <el-menu-item :index="item.path" :key="item.path" @click="jumpPage(item)">
                    <i :class="'el-icon-'+item.icon"></i>
                    <span slot="title" v-text="item.label"></span>
                </el-menu-item>
            </template>
            <template v-else>
                <el-submenu :key="item.label" :index="item.label">
                    <template slot="title">
                        <i :class="'el-icon-'+item.icon"></i>
                        <span slot="title" v-text="item.label"></span>
                    </template>
                    <el-menu-item v-for="subitem in item.children" @click="jumpPage({parent: item.label,...subitem})" :index="subitem.path" :key="subitem.path">{{subitem.label}}</el-menu-item>
                </el-submenu>
            </template>
        </template>
    </el-menu>
</template>

<script>
export default {
    name: 'Aside',
    data() {
      return {
        menuList: [
            {
                path: '/home',
                name: 'home',
                label: '首页',
                icon: 's-home',
                url: 'Home/Home'
            },
            {
                path: '/mall',
                name: 'mall',
                label: '商城管理',
                icon: 's-shop',
                url: 'MallManage/MallManage'
            },
            {
                path: '/user',
                name: 'user',
                label: '用户管理',
                icon: 'user-solid',
                url: 'UserManage/UserManage'
            },
            {
                label: '其他',
                icon: 's-opportunity',
                children: [
                    {
                        path: '/page1',
                        name: 'page1',
                        label: '页面1',
                        icon: '',
                        url: 'Other/PageOne'
                    },
                    {
                        path: '/page2',
                        name: 'page2',
                        label: '页面2',
                        icon: '',
                        url: 'Other/PageTwo'
                    }
                ]
            },
        ],
      };
    },
    methods: {
        jumpPage(item){
            this.$router.push(item.path)
            this.$store.commit("breakCrump",item)
        },
    },
    computed: {
        isCollapse() {
            return this.$store.state.tab.isCollapse
        }
    },
}
</script>

<style lang="scss" scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 180px;
    min-height: 400px;
}
.el-menu{
    height: 100vh;
    border: 0;
    overflow: hidden;
    h3{
        line-height: 54px;
        color: #fff;
        font-size: 18px;
        font-weight: 500;
        text-align: center;
        overflow: hidden;
        white-space: nowrap;
    }
}
</style>
