<template>
    <header id="header">
        <div class="breakcrumb">
            <i class='el-icon-s-unfold' @click="dropMenu" style="font-size: 26px" :class="isCollapse?'rotateZ':''"></i>&nbsp;&nbsp;
            <el-breadcrumb separator="/">
                <el-breadcrumb-item v-for="item in tabList" :to="{ path: item.path }" :key="item.path">{{item.label}}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>      
        <div>
            <el-dropdown trigger="click" class="avar">
                <span>
                    <el-avatar :src="avaImg" :size="42"></el-avatar>              
                    <i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item>个人中心</el-dropdown-item>
                    <el-dropdown-item @click.native="exitPage">退出</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </header>
</template>

<script>
import {mapState} from 'vuex'
export default {
    name: 'NavHeader',
    data() {
        return {
            avaImg: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
            isCollapse: this.$store.state.tab.isCollapse,
        }
    },
    methods: {
        dropMenu() {
            this.$store.commit("drop")
            this.isCollapse = this.$store.state.tab.isCollapse
        },
        exitPage() {
            this.$store.commit('clearToken')
            this.$router.push({name: 'login'})
        }
    },
    computed: {
        ...mapState({
            tabList: state => state.tab.tabList
        })
    }
}
</script>

<style lang="scss" scoped>
    #header{
        height: 60px;
        color: #606266;
        padding: 10px 15px;
        box-sizing: border-box;
        background-color: #f2f6fc;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .breakcrumb{
            display: flex;
            align-items: center;
            .el-icon-s-unfold{
                transition: all 0.5s ease;
                cursor: pointer; 
                width: 40px;
                height: 40px;
                line-height: 40px;
                text-align: center;
            }
            .rotateZ{
                transform: rotateZ(180deg);
            }
        }    
        
        .avar{
            cursor: pointer;
        }
    }
    
</style>